import React from 'react';
import ReactTable from 'react-table';
import ReactTooltip from 'react-tooltip';
import {fetchVerifications, linkVerification} from '../utils';
import {Button, Modal, Popconfirm} from 'antd';
import {verificationColumns, verificationTxColumns} from '../sharedTableData';

export default class Verifications extends React.Component {

    state = {
        verifications: [],
        transactions: [],
        modalSelected: null,
        modalReview: null,
    };

    tableColumns = [...verificationColumns, {
        Header: '',
        id: 'Link',
        className: 'cell-button',
        Cell: (props) => {
            if (props.original.Id) {
                return (
                    <Button htmlType="button" block type="primary" size="small" onClick={() => this.openReviewModal(props.original)}>
                        Review
                    </Button>
                );
            }
            else {
                return (
                    <Button htmlType="button" block size="small" onClick={() => this.openModal(props.original)}>
                        Link to Transaction
                    </Button>
                );
            }
        },
    }];

    modalTableColumns = [...verificationTxColumns, {
        Header: '',
        id: 'Link',
        className: 'cell-button',
        width: 100,
        Cell: (props) => {
            return (
                <Popconfirm
                    onConfirm={() => this.linkTransactions(this.state.modalSelected.BCId, props.original.Id)}
                    okText="Yes"
                    cancelText="No"
                    title="Are you sure?">
                    <Button htmlType="button" block type="primary" size="small">Link</Button>
                </Popconfirm>
            );
        },
    }];

    componentDidMount() {
        this.fetchData();
    }

    fetchData = () => {
        fetchVerifications().then(data => {
            const verifications = data.filter(tx => !!tx.BCId);
            const transactions = data.filter(tx => !!tx.Id);

            this.setState({
                verifications,
                transactions,
            }, () => {
                ReactTooltip.rebuild()
            });
        });
    };

    openModal = (data) => {
        this.setState({
            modalSelected: data,
        }, () => {
            ReactTooltip.rebuild()
        });
    };

    closeModal = () => {
        this.setState({
            modalSelected: null,
        });
    };

    openReviewModal = (data) => {
        this.setState({
            modalReview: data,
        }, () => {
            ReactTooltip.rebuild()
        });
    };

    closeReviewModal = () => {
        this.setState({
            modalReview: null,
        });
    };

    confirmReview = () => {
        this.linkTransactions(this.state.modalReview.BCId, this.state.modalReview.Id);
    };

    linkTransactions = (BCId, TwId) => {
        this.setState({
            modalSelected: null,
            modalReview: null,
        });

        linkVerification(BCId, TwId, this.props.user.email).then(() => this.fetchData());
    };

    render() {
        return (
            <div>
                <ReactTable
                    data={this.state.verifications}
                    columns={this.tableColumns}
                    resizable={false}
                    getTdProps={(state, row, column) => ({
                        'data-for': 'celltooltip',
                        'data-tip': (row && column.id) ? row.row[column.id] : '',
                    })}/>

                <Modal
                    visible={!!this.state.modalSelected}
                    title="Link Transaction to Verification Request"
                    width="90%"
                    footer={null}
                    onCancel={this.closeModal}
                    destroyOnClose>
                    <h4>Selected Request</h4>
                    <ReactTable
                        data={[this.state.modalSelected]}
                        columns={verificationColumns}
                        minRows={1}
                        sortable={false}
                        showPagination={false}
                        resizable={false}
                        getTdProps={(state, row, column) => ({
                            'data-for': 'celltooltip',
                            'data-tip': (row && column.id) ? row.row[column.id] : '',
                        })}/>

                    <br/>
                    <h4>Transactions</h4>
                    <ReactTable
                        data={this.state.transactions}
                        columns={this.modalTableColumns}
                        resizable={false}
                        minRows={1}
                        getTdProps={(state, row, column) => ({
                            'data-for': 'celltooltip',
                            'data-tip': (row && column.id) ? row.row[column.id] : '',
                        })}/>

                    <ReactTooltip
                        id="celltooltip"
                        effect="solid"
                        place="top"
                        delayShow={1000}
                        offset={{top: -15}}
                        className="cell-tooltip-style"/>
                </Modal>

                <Modal
                    visible={!!this.state.modalReview}
                    title="Review"
                    width="90%"
                    footer={(
                        <Button
                            type="primary"
                            htmlType="button"
                            onClick={this.confirmReview}>
                            Confirm
                        </Button>
                    )}
                    onCancel={this.closeReviewModal}
                    destroyOnClose>
                    <h4>Verification request</h4>
                    <ReactTable
                        data={[this.state.modalReview]}
                        columns={verificationColumns}
                        minRows={1}
                        sortable={false}
                        showPagination={false}
                        resizable={false}
                        getTdProps={(state, row, column) => ({
                            'data-for': 'celltooltip',
                            'data-tip': (row && column.id) ? row.row[column.id] : '',
                        })}/>

                    <br/>
                    <h4>Transaction</h4>
                    <ReactTable
                        data={[this.state.modalReview]}
                        columns={verificationTxColumns}
                        minRows={1}
                        sortable={false}
                        showPagination={false}
                        resizable={false}
                        getTdProps={(state, row, column) => ({
                            'data-for': 'celltooltip',
                            'data-tip': (row && column.id) ? row.row[column.id] : '',
                        })}/>

                    <ReactTooltip
                        id="celltooltip"
                        effect="solid"
                        place="top"
                        delayShow={1000}
                        offset={{top: -15}}
                        className="cell-tooltip-style"/>
                </Modal>

                <ReactTooltip
                    id="celltooltip"
                    effect="solid"
                    place="top"
                    delayShow={1000}
                    offset={{top: -15}}
                    className="cell-tooltip-style"/>
            </div>
        );
    }
}
